import React, { Component } from 'react'
import './Notepad.css'
import Win, {optionisOpen} from './../../Win/Win';
export default class Notepad extends Component {
    constructor(props){
        super(props);
        this.state = {
            opt:false,
            fontSize:16,
            color:"black",
            BackgroundColor:String("fff"),
            fontWeight:400

        }
    }
    
    render() {
        let s = this.state;
        let textareaStyle = {
            fontSize:s.fontSize,
            color:s.color,
            backgroundColor:s.background,
            fontWeight:s.fontWeight
        }
        return (
            <Win
            
            app={<div>
                
                <textarea name="" id="texts" style={textareaStyle}></textarea>
                <div className={"options"} style={(s.opt)?{display:"block"}:{display:"none"}}>
                    
                        <div id="head">Font Size</div>   <div id="option" ><input type="number" value={s.fontSize} onChange={
                            e=>{
                                this.setState({
                                    fontSize:Number(e.target.value) 
                                })}
                        }/></div>
                    
                    
                        <div id="head">Font Color</div>   <div id="option" ><input type="color" value={s.color} onChange={
                            e=>{
                                this.setState({
                                    color:String(e.target.value)
                                })}
                        }/></div>
                        <div id="head">Background Color</div>   <div id="option" ><input type="color" value={s.background} onChange={
                            e=>{
                                this.setState({
                                    background:String(e.target.value)
                                })}
                        }/></div>
                        <div id="head">Font Weight</div>   <div id="option" >
                            <select onChange={
                                e=>{
                                    this.setState({
                                        fontWeight:Number(e.target.value)
                                    })
                                }
                            }>
                                <option value="100">100</option>
                                <option value="200">200</option>
                                <option value="300">300</option>
                                <option value="400" selected>400</option>
                                <option value="500">500</option>
                                <option value="600">600</option>
                                <option value="700">700</option>
                                <option value="800">800</option> <option value="900">900</option>
                            </select>
                        </div>
                    
                    
                </div>
                </div>}
                title={"NOTEPAD"}
            optionButton={
                <button onClick={
                    e=>{
                        // eslint-disable-next-line default-case
                        switch(this.state.opt){
                            case true:
                                this.setState({
                                    opt:false
                                })
                            break;
                            case false:
                                this.setState({
                                    opt:true
                                })
                            break;
                        }
                    }
                } className={"option"}></button>
            }

            

            />
        )
    }
}
