import React, { Component } from 'react'
import TaskBar from './Components/TaskBar/TaskBar'
import Win from './Components/Win/Win'
import Notepad from './Components/Apps/Notepad/Notepad'
import './OS.css'
export default class OS extends Component {
    constructor(){
        super();

        this.state ={
            notepad:false
        }
    }
    componentDidMount(){
        document.addEventListener("keydown", e=>{
            if(e.key=='tab'){
                e.preventDefault()
            }
        })
    }
    
    render(){
        return (
            <div className={"OS"} style={{overflow:"none",backgroundImage:"url('./wall.png')"}}>
             

              
              {(this.state.notepad)?<Notepad />:null}
              <TaskBar 
              ele={[
                "NotePad",
                "MSPAINT"
            ]}
            elefns={[
                e=>{e.target.style.background = "rgba(0,0,0,0.5)";
                this.setState({
                    notepad:true
                })
                
                },
                e=>{alert('')}
            ]}
            
            />
            </div>
        )
    }
}
