import React, { Component } from 'react'

import './TaskBar.css'
export default class TaskBar extends Component {
    constructor(props){
        super(props);
        this.state={
            value:props.value
        }
    }
    render() {
        let s = this.state;
        return (
            <div className="TaskBar">
                <div className='Start'></div>
        <div className={"elements"}>
            {this.props.ele.map((i,j) =>{
               return <button title={i} key={i} onClick={this.props.elefns[j]}>{i}</button>
            })}
            </div>
            </div>
        )
    }
}
